!**
 * Nav, global for mobile & tablet
 *!
ul.nav{
  margin: 0; display: none;
  &.level-0 {
    a {
      color: $main-color; padding: 15px 30px; text-align: center; font-size: 22px;
    }
  }
}
* {
  margin: 0;
  padding: 0;
}

.burger-close {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -6px;
  width: 20px; height: 12px; cursor: pointer;
  .transform(rotate(0deg));
  .transition(@duration:.5s, @ease:ease-in-out);
  span {
    display: block; position: absolute; left: 0;
    height: 2px; width: 100%; background: #909090; opacity: 1;
    .transform(rotate(0deg));
    .transition(@duration:.25s, @ease:ease-in-out);
  }
}
.burger-close {
  span:nth-child(1) {
    top: 0px;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
    -o-transform-origin: left center;
    transform-origin: left center;
  }
  span:nth-child(2) {
    top: 5px;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
    -o-transform-origin: left center;
    transform-origin: left center;
  }
  span:nth-child(3) {
    top: 10px;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
    -o-transform-origin: left center;
    transform-origin: left center;
  }
}
.topnav-wrapper.open .burger-close {
  span:nth-child(1) {
    .transform(rotate(45deg)); top: -2px; left: 3px;
  }
  span:nth-child(2) { width: 0%; opacity: 0; }
  span:nth-child(3) {
    .transform(rotate(-45deg)); top: 12px; left: 3px;
  }
}
#burger_left {
  position: relative;
  .burger-close { left: 20px; }
  a { font-family: robotoregular, sans-serif; font-size: 18px; color: @main-color; padding: 16px 20px 16px 55px;
    height: @menu_height; display: block; line-height: 1; text-decoration: none;
  }
}

#burger_right {
  @picto_size : 32px;
  position: relative; float: right;
  //Logged or unlogged
  //ico for unlogged, user picture for logged
  &:before,
  figure { margin: 0; opacity: 1; font-size: @picto_size; height: @picto_size; padding: 9px 20px; .transition(@duration:.5s, @ease:ease-in-out); }
  .burger-close { opacity: 0; left: 26px; }
  figure {
    height: @menu_height;
    img {
      height: @picto_size; width: @picto_size; .rounded(50%); overflow: hidden; display: block;
    }
  }
}
.topnav-wrapper.open #burger_right {
  //Logged or unlogged
  &:before,//ico for unlogged
  figure{ opacity: 0; }//user picture for logged
  .burger-close {
    opacity: 1;
  }
}

.topnav { float: none; }
.topnav-wrapper {
  position: fixed; top: 0; z-index: 10; background-color: transparent;
  &.open {
    z-index: 11;
    overflow-y: scroll; height: 100%; width: 100%; background-color: $third-color;
    ul.nav {
      width: 100%; display: block;
      li {
        float: none; display: block;
        &.visible-desktop { display: none; }
      }
    }
  }
}
#topnav_left { left: 0; }
#topnav_right { right: 0; }

!**
 * Nav, first level
 *!
!* User *!
ul.nav.level-0 li.user {
  figure {
    float: none;
    figcaption {
      color: $main-color;
    }
  }
}

!**
 * Nav, second level
 *!
ul.nav ul.level-1 {
  display: block;
  position: relative;
  top: initial;
  right: auto;
  left: auto;
  min-width: 0;
